import React, { useEffect } from "react";
import { useRef } from "react";

export function GraphVisualizer({
  canvasRef,
  data,
  isChange,
  startingPoint,
  textColor,
  mainHeight,
  mainWidth,
}) {
  if (!canvasRef) {
    canvasRef = useRef(null);
  }

  useEffect(() => {
    /* -------------------------------------------------------------------------- */
    /*                             initializing canvas                            */
    /* -------------------------------------------------------------------------- */
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");

    /* -------------------------------------------------------------------------- */
    /*                 creating a canvas of some height and width                 */
    /* -------------------------------------------------------------------------- */
    ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

    let pos = {};
    let connections = {};
    let visited = {};

    /* -------------------------------------------------------------------------- */
    /*                          recursing graph with dfs                          */
    /* -------------------------------------------------------------------------- */
    recurseGraph(visited, startingPoint, 100, 300, ctx, pos, connections, canvas);

  }, [isChange]);

  const recurseGraph = (vis, node, x, y, ctx, pos, connections, canvas) => {
    if (!vis[node]) {
      const width = 100; // Rectangle width
      const height = 30; // Rectangle height

      const scaleFactor = canvas.width / mainWidth; // Adjust this based on canvas dimensions
      x *= scaleFactor;
      y *= scaleFactor;

      if (x < width / 2) x = width / 2;
      if (x > canvas.width - width / 2) x = canvas.width - width / 2;
      if (y < height / 2) y = height / 2;
      if (y > canvas.height - height / 2) y = canvas.height - height / 2;

      ctx.strokeRect(x - width / 2, y - height / 2, width, height);
      ctx.fillStyle = textColor;
      ctx.textAlign = "center";
      ctx.textBaseline = "middle";
      ctx.fillText(node, x, y);

      pos[node] = { x: x, y: y };

      vis[node] = true;
      const neighbors = data[node];
      const neighborCount = neighbors.length;
      let yOffset = -(neighborCount - 1) * 50 * scaleFactor;

      for (let i = 0; i < neighborCount; i++) {
        const neighbor = neighbors[i];
        const newX = x + 200 * scaleFactor;
        const newY = y + yOffset;

        if (!connections[`${node}-${neighbor}`] && !connections[`${neighbor}-${node}`]) {
          ctx.beginPath();
          ctx.moveTo(x + width / 2, y);
          ctx.lineTo(
            pos[neighbor] ? pos[neighbor].x - width / 2 : newX - width / 2,
            pos[neighbor] ? pos[neighbor].y : newY
          );
          ctx.stroke();
          connections[`${node}-${neighbor}`] = true;
        }

        recurseGraph(vis, neighbor, newX, newY, ctx, pos, connections, canvas);
        yOffset += 100 * scaleFactor;
      }
    }
  };

  return <canvas ref={canvasRef} width={mainWidth} height={mainHeight} />;
}

GraphVisualizer.defaultProps = {
  mainHeight: 400,
  mainWidth: 500,
  textColor: "black",
  data: {},
};

export default GraphVisualizer;
